import React from 'react';
import { 
  BrowserRouter as Router, 
  Routes, 
  Route, 
  Navigate 
} from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material';

// Componentes de Autenticao
import LoginPage from './views/Auth/LoginPage';
import SignupPage from './views/Auth/SignupPage';

// Componentes do Voluntrio
import VolunteerDashboard from './views/Volunteer/OpportunityList'; // Continua sendo a lista de oportunidades para Voluntrios
import SubscriptionForm from './views/Volunteer/SubscriptionForm'; // Formulrio de candidatura

// Componentes da ONG
import NGOMainDashboard from './views/NGO/NGOMainDashboard'; // O NOVO Dashboard principal da ONG
import NGOVolunteers from './views/NGO/NGOVolunteers'; // O antigo NGODashboard, agora para lista de voluntrios
import GerenciarOportunidades from './views/NGO/GerenciarOportunidades'; // O componente de gerenciamento de vagas
import VolunteerDetail from './views/NGO/VolunteerDetail'; // Detalhes do voluntrio para ONG

// Tema Material-UI
const theme = createTheme({
  palette: {
    primary: { main: '#2E7D32' }, 
    secondary: { main: '#1976D2' }, 
  },
});

export default function App() {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Routes>
          {/* Rotas de Autenticao */}
          <Route path="/login" element={<LoginPage />} />
          <Route path="/signup" element={<SignupPage />} />

          {/* Rotas para o Voluntrio */}
          <Route path="/volunteer" element={<VolunteerDashboard />} /> {/* Lista de oportunidades para voluntrio */}
          <Route path="/volunteer/opportunity/:id" element={<SubscriptionForm />} /> {/* Formulrio de inscrio */}

          {/* Rotas para a ONG */}
          <Route path="/ngo" element={<NGOMainDashboard />} /> {/* NOVO: Dashboard principal da ONG com opes */}
          <Route path="/ngo/voluntarios" element={<NGOVolunteers />} /> {/* NOVO: Rota para a lista de voluntrios da ONG */}
          <Route path="/ngo/oportunidades" element={<GerenciarOportunidades />} /> {/* NOVO: Rota para o gerenciamento de oportunidades da ONG */}
          <Route path="/ngo/volunteer/:id" element={<VolunteerDetail />} /> {/* Detalhes de voluntrio para ONG */}

          {/* Redirecionamentos padro */}
          <Route path="/" element={<Navigate to="/login" />} /> {/* Redireciona a raiz para o login */}
          <Route path="*" element={<Navigate to="/login" />} /> {/* Redireciona rotas no encontradas para o login */}
        </Routes>
      </Router>
    </ThemeProvider>
  );
}